.display {
    display: flex;
    list-style: none;
    justify-content: space-between;
}

.reel {
    display: flex;
    list-style: none;
    justify-content: space-between;
}

.logo {
    margin-left: 1em;
}

.nbc {
    position: relative;
    left: 2em;
}

.text {
    font-size: .7rem;
    font-weight: bolder;
    margin-left: 1em;
}

.padding {
    padding-left: 2em;
    font-family: Nunito Sans;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    height: 52px;
    cursor: pointer;
}

.background {
    background: rgba(255, 255, 255, 0.85);
    backdrop-filter: blur(28px);
    height: 80px;
    padding-top: 2em;
}

.search {
    position: relative;
    right: 4em;
}

.icon {
    margin-left: 5em;
}

@media (max-width: 1200px) {
    .reel {
        display: none;
    }

  }
