* {
  box-sizing: border-box;
}

.App {
  background: #E5E5E5;
  padding: 12px;
}

.displayed {
  display: flex;
  justify-content: space-between;
  margin-right: 3em;
  margin-top: 1em;
}

.both {
  display: flex;
  list-style: none;
  justify-content: space-between;
}

.view {
  border: 1px solid rgba(125, 24, 21, .60);
  border-radius: 2px;
  position: relative;
  left: 20em;
  top: 1em;
  width: 15em;
  height: 4em;
  display: flex;
  margin-top: 3em;
  cursor: pointer;
}

.h3 {
  font-size: 14px;
  line-height: 15px;
  letter-spacing: 1.6px;
  font-weight: 500;
  color: #c31815;
  position: absolute;
  top: 1.7em;
  left: 5em;
}

.edit {
  display: flex;
}

.divider {
  margin-bottom: 6em;
}

.picks {
  font-family: Poppins;
font-style: normal;
font-weight: 600;
font-size: 18px;
line-height: 27px;
display: flex;
align-items: center;
color: #2A2A2A;
margin-bottom: 1em;
}

.dist {
  display: flex;
}

.rectangle {
  border: 1px solid #c31815;
  background: #c31815;
  height: 4px;
  width: 25px;
}

.rectangles {
  display: flex;
  justify-content: space-around;
  width: 12%;
  margin: 1.5em auto 3.5em auto;
}

.rectangled {
  opacity: 0.2;
}

@media (max-width: 1200px) {
  .both{
      display: block;
  }

  .location {
    position: relative;
    top: 28em;
  }

  .displayed {
    display: block;
    margin: auto;
  }

  .view {
    width: 250px;
    position: relative;
    top: 4em;
    left: 5em;
  }

  .dist {
    position: relative;
    top: 18em;
  }

  .rectangle {
    width: 10px;
  }
}
