.location {
    position: relative;
    bottom: 11em;
}

.news {
    margin-bottom: 2em;
}

.get {
    margin: 0 0 0 2em;
}

form {
    background: #fff;
    height: 35%;
    width: 370px;
    border-radius: 2px;
    padding-top: 2em;
}

input {
    padding: 12px;
    margin-left: 1em;
    width: 80%;
    border-radius: 3px;
    border: 1px solid rgba(0, 0, 0, .10);
}

.im {
    width: 40%;
}


button {
    padding: 8px;
    margin-top: 15px;
    border-radius: 2px;
    background: #c31815;
    margin-left: 2em;
    border: none;
    font-weight: 500;
    font-size: 14px;
    line-height: 15px;
    letter-spacing: 1.6px;
    text-align: center;
    height: 40px;
    width: 100px;
}

@media (max-width:1200px) {
    button {
        margin-bottom: 35px;
    }

    form {
        width: 100%;
    }
}

