.card {
    box-shadow: 0px 2px 14px rgba(42, 42, 42, .24);
    border-radius: 3px;
    padding: 5px;
    width: 33%;
}

.lewis {
    padding: 15px;
}

.log {
    margin-left: 2em;
}

.merge {
    margin: 0 auto 1em auto;
}

.line {
    width: 100%;
    border-top: 1px solid #2a2a2a;
    opacity: .1;
    margin-bottom: 1.3em;
}

.john {
width: 20em;
font-size: 18px;
line-height: 27px;
font-weight: 600;
color: #2a2a2a;
/* padding: 15px; */
}

.rep {
    width: 25em;
    text-align: start;
    font-size: 15px;
    line-height: 22px;
    font-weight: 400;
    position: relative;
}

@media (max-width: 1200px) {
    .card {
        width: 100%;
        position: relative;
        top: 21em;
    }

    .rep, .john, .lewis {
        width: 100%;
    }
}
