.trending {
    margin-top: 4em;
    margin-right: 3em;
    width: 70%;
}

p {
    color: #2A2A2A;
}

.logo {
    display: flex;
    justify-content: space-between;
}

.logos {
    margin-right: 1.8em;
}

.trend {
    color: #C31815;
    font-family: Poppins;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 29px;
    letter-spacing: 0.02em;
    position: relative;
    right: 1em;
}

.cake {
font-family: IBM Plex Serif;
font-weight: 600;
font-size: 2rem;
line-height: 70%;
color: #2A2A2A;
}

h5 {
width: 15em;
font-size: 32px;
line-height: 41.6px;
font-weight: 600;
}

.earlier {
    font-family: Poppins;
    font-size: 15px;
    line-height: 24.75px;
    line-height: 165%;
    text-align: start;
    color: #2A2A2A;
    font-weight: 400;
    width: 95%;
}

.hours {
    display: flex;
    font-weight: 400;
    font-size: 13px;
    line-height: 21.45px;
    margin-top: 4em;
}

.lucy {
    opacity: 70%;
    position: relative;
    left: 2em;
    top: auto;
}

@media (max-width: 1200px) {
    .reel {
        display: none;
    }

    .trending {
        position: relative;
        bottom: 20em;
        width: 100%;
    }

    .earlier {
        position: relative;
        top: 20em;
        width: 100%;
    }

    .hours {
        position: relative;
        top: 22em;
    }
  }
