.editing {
    display: flex;
}

.margins {
    margin-left: 2em;
}

.making {
    font-family: IBM Plex Serif;
font-style: normal;
font-weight: bold;
font-size: 18px;
line-height: 27px;
color: #2A2A2A;
width: 85%;
}

.authorities {
    font-family: Nunito Sans;
font-style: normal;
font-weight: normal;
font-size: 16px;
line-height: 163.41%;
color: #2A2A2A;
width: 90%;
text-align: left;
position: relative;
top: -0.1em;
}

.ima {
    width: 100%;
}

.imas {
    width: 100%;
}

@media (max-width:1200px) {
    .imas {
        display: none;
    }

    .editings {
        display: none;
    }

    .editing {
        width: 250%;
        margin-top: 18em;
    }
    
    .authorities {
        display: none;
    }

    .margins {
        position: relative;
        right: 30em;
        width: 45%;
        top: 11em;
    }

    .making {
        color: #fff;
    }
}
