.displays {
    display: flex;
    justify-content: space-around;
}

.disp {
    display: flex;
}

ul li {
    list-style: none;
}

.padding {
    padding-left: 2em;
}

.privacy {
    margin-top: 10px;
}

.footer {
    background: #0e1e32;
    color: #fff;
    height: 100%;
    width: 100%;
}

.white {
    margin-left: 65em;
    width: 15%;
    display: flex;
    justify-content: space-between;
    position: relative;
    bottom: 4em;
}

.move {
    padding-top: 5em;
    position: relative;
}

.copyright {
    font-family: Poppins;
font-style: normal;
font-weight: normal;
font-size: 12px;
line-height: 25px;
letter-spacing: 0.02em;
color: #FFFFFF;
}

.footImage {
position: relative;
left: 1.7em;
}

@media (max-width:1200px) {
    .footImage {
        width: 3.5em;
        height: 3em;
        position: relative;
        left: 1.5em;
        bottom: 4em;
    }

    .disp {
        display: block;
        position: relative;
        right: 2em;
    }

    .copyright {
        position: absolute;
        margin: 27em auto auto 11em;
    }

    .private {
        position: relative;
        right: 5em;
    }

    .last {
        width: 100%;
        border-top: 2px solid #ffffff;
        opacity: .1;
        position: relative;
        bottom: 2em;
        display: flex;
        justify-content: center;
    }

    .white {
        margin: 4em auto auto auto;
        width: 50%;
    }

    .padding {
        letter-spacing: 0.02em;
        margin-top: -12px;
    }

    .footer {
        height: 26em;
    }
}
