.right {
    background: #E5E5E5;
}

.beloved {
    font-weight: 700;
    font-size: 18px;
    line-height: 27px;
    width: 20em;
    position: relative;
    top: 15.5em;
}

button {
    color: #fff;
    background: #c31815;
    font-size: 14px;
    line-height: 15px;
    letter-spacing: 1.6px;
    text-align: center;
    font-weight: 500;
}

.images {
    width: 30%;
    position: absolute;
    height: 40%;
}

.live {
    font-family: Poppins;
    font-weight: 600;
    font-size: 18px;
    line-height: 27px;
    display: flex;
    align-items: center;
    color: #C31815;
}

.circle1 {
    width: 14px;
    height: 14px;
    background: #C31815;
    border-radius: 50px;
    margin-left: 1em;
}

.circle2 {
    width: 20px;
    height: 20px;
    opacity: 0.8;
    border: 0.5px solid #C31815;
    border-radius: 50px;
    position: relative;
    right: 17px;
    bottom: .2em;
}

.circle3 {
    border: 0.5px solid #c31815;
    border-radius: 50px;
    width: 26px;
    height: 26px;
    opacity: 0.4;
    position: relative;
    right: 40px;
    bottom: .4em;
}

.circles {
    display: flex;
    margin-bottom: 1em;
}

@media (max-width: 1200px) {
    .beloved {
        position: absolute;
        top: 56em;
        width: 95%;
    }

    .images {
       width: 93%;
       position: absolute;
       bottom: -23em;
    }

    .right {
        width: 100%;
    }
}
