.sub {
    display: flex;
    background: rgba(255, 255, 255, .85);
    backdrop-filter: blur(28px);
    padding: 1.5em;
}

.sub li {
    font-size: 18px;
    line-height: 27px;
    font-weight: 600;
    color: #2A2A2A;
}

.margin {
    margin-left: 3em;
}

@media (max-width: 1200px) {
    .sub {
        position: relative;
        top: 21em;
    }
}
